import React from 'react'
import swal from 'sweetalert'
import * as svg from "../../../../../utils/svg"

interface Props {
    svgData: {
        _id: string
        svgURL: string
        svgString: string
        tags: string[]
        created_at: string
        updated_at: string
    }
    deleteSvg: any
}

const RenderSvg: React.FC<Props> = ({ svgData, deleteSvg }) => {

    const svgUrl = svg.svgToURL(svgData.svgString)

    const handleDeleteSvg = () => {
        swal("Are you sure?", "You want to delete this svg", "Warning")
    }

    return (
        <div className="flex justify-between pr-2 pl-2">
            <div>
                <button className="border border-gray95" style={{ width: 100, height: 100 }} >
                    <img alt="svg" src={svgUrl} />
                </button>
            </div>
            <div>
                <button
                    onClick={handleDeleteSvg}
                    className="bg-redish hover:bg-red600 text-white font-bold py-2 px-4 rounded">Delete</button>
            </div>
        </div>
    )
}

export default RenderSvg
